// Color system
//
// Body, heading color base
$body-color-base:       #717171;
$heading-color-base:    #202020;

// Color settings
$main-color:            #3498db;

$white:                 #fff;
$black:                 #000;

$white-25:              rgba($white, .25);
$white-50:              rgba($white, .5);
$white-75:              rgba($white, .75);
$black-25:              rgba($black, .25);
$black-50:              rgba($black, .5);
$black-75:              rgba($black, .75);

$blue:                  #3498db;
$indigo:                #2874a6;
$purple:                #8e44ad;
$pink:                  #d98880;
$red:                   #e94b35;
$orange:                #d4ac0d;
$yellow:                #f1c40f;
$green:                 #2ecc71;
$teal:                  #45b39d;
$cyan:                  #85c1e9;
$light-gray:            #f7f9f9;

$facebook:              #2874a6;
$twitter:               #5dade2;
$pinterest:             #b03a2e;
$google-plus:           #e74c3c;
$instagram:             #af7ac5;
$dribbble:              #cd6155;
$youtube:               #cb4335;
$vimeo:                 #85c1e9;
$stumbleupon:           #e67e22;

// Dark viridian shade
$viridian-100:          #2c3e50;
$viridian-200:          #273746;
$viridian-300:          #212f3d;
$viridian-400:          #1c2833;
$viridian-500:          #17202a;

$viridians: ();
$viridians: map-merge(
  (
    "viridian-100":     $viridian-100,
    "viridian-200":     $viridian-200,
    "viridian-300":     $viridian-300,
    "viridian-400":     $viridian-400,
    "viridian-500":     $viridian-500,
  ),
  $viridians
);

$secondary:             $teal;
$light:                 $light-gray;
$dark:                  $viridian-100;

// Extended colors
$colors: (
  "light-gray":         $light-gray,
  "dark-viridian":      $viridian-100,
  "facebook":           $facebook,
  "twitter":            $twitter,
  "pinterest":          $pinterest,
  "google-plus":        $google-plus,
  "instagram":          $instagram,
  "dribbble":           $dribbble,
  "youtube":            $youtube,
  "vimeo":              $vimeo,
  "stumbleupon":        $stumbleupon
);

// Extended theme colors
$theme-colors: (
  "facebook":           $facebook,
  "twitter":            $twitter,
  "pinterest":          $pinterest,
  "google-plus":        $google-plus,
  "instagram":          $instagram,
  "dribbble":           $dribbble,
  "youtube":            $youtube,
  "vimeo":              $vimeo,
  "stumbleupon":        $stumbleupon
);

// Customize theme light text color.
$theme-light-text-color:                $viridian-100;

// Options
//
// Quickly modify global styling by enabling or disabling optional features.
$enable-rounded-theming:                true;
$enable-rounded-button:                 true;
$enable-rounded-form-input:             true;
$enable-rounded-card:                   true;
$enable-shadows-theming:                true;

// Body
//
// Settings for the `<body>` element.
$body-color:                            $body-color-base;

// Links
//
// Style anchor elements.
$link-color:                            $main-color;
$link-hover-color:                      lighten($link-color, 15%);

// Components
//
// Define common padding and border radius sizes transition and more.
$component-active-bg:                   $main-color;

$border-width:                          1px;

$border-radius:                         .25rem;
$border-radius-lg:                      .3125rem;
$border-radius-sm:                      .1875rem;

$transition-duration-base:              .2s;
$transition-timing-base:                ease-in-out;

$hover-fill-opacity:                    1;
$hover-fade-opacity:                    .8;

$hover-overlay-bg:                      rgba($black, .65);
$hover-element-slide:                   -30px;

$box-shadow-sm:                         0 .125rem .3rem rgba($black, .095);
$box-shadow:                            0 .42rem 1.22rem rgba($black, .12);
$box-shadow-md:                         0 .67rem 2.15rem rgba($black, .14);
$box-shadow-lg:                         0 1rem 3.2rem rgba($black, .165);

$circle-border-radius:                  50%;

$gradient-color-top:                    rgba($black, 0);
$gradient-color-bottom:                 rgba($black, .4);
$gradient-location-top:                 0%;
$gradient-location-bottom:              100%;

$caret-size:                            1.2em;

$disabled-opacity:                      .65;

// Fonts
//
// Font, line-height, and color for body text, headings, and more.
$font-family-base: "Montserrat", sans-serif;

$font-size-base:    1rem;
$font-size-lg:      ($font-size-base * 1.125);
$font-size-sm:      ($font-size-base * .75);

$font-weight-100:   100;
$font-weight-200:   200;
$font-weight-300:   300;
$font-weight-400:   400;
$font-weight-500:   500;
$font-weight-600:   600;
$font-weight-700:   700;
$font-weight-800:   800;
$font-weight-900:   900;

$font-weight: ();
$font-weight: map-merge(
  (
    "100": $font-weight-100,
    "200": $font-weight-200,
    "300": $font-weight-300,
    "400": $font-weight-400,
    "500": $font-weight-500,
    "600": $font-weight-600,
    "700": $font-weight-700,
    "800": $font-weight-800,
    "900": $font-weight-900
  ),
  $font-weight
);

$h1-font-size:                          $font-size-base * 2.5;
$h2-font-size:                          $font-size-base * 2;
$h3-font-size:                          $font-size-base * 1.75;
$h4-font-size:                          $font-size-base * 1.5;
$h5-font-size:                          $font-size-base * 1.25;
$h6-font-size:                          $font-size-base;

$h1-font-size-sm:                       $font-size-base * 2;
$h2-font-size-sm:                       $font-size-base * 1.75;
$h3-font-size-sm:                       $font-size-base * 1.5;
$h4-font-size-sm:                       $font-size-base * 1.25;
$h5-font-size-sm:                       $font-size-base;
$h6-font-size-sm:                       $font-size-base * .875;

$headings-font-weight:                  600;
$headings-color:                        $heading-color-base;

$display1-size:                         $font-size-base * 6;
$display1-size-xs:                      $display1-size * .5;
$display1-size-md:                      $display1-size * .75;
$display1-size-xl:                      $display1-size;

$display2-size:                         $font-size-base * 5.5;
$display2-size-xs:                      $display2-size * .5;
$display2-size-md:                      $display2-size * .75;
$display2-size-xl:                      $display2-size;

$display3-size:                         $font-size-base * 4.5;
$display3-size-xs:                      $display3-size * .5;
$display3-size-md:                      $display3-size * .75;
$display3-size-xl:                      $display3-size;

$display4-size:                         $font-size-base * 3.5;
$display4-size-xs:                      $display4-size * .5;
$display4-size-md:                      $display4-size * .75;
$display4-size-xl:                      $display4-size;

$blockquote-font-size:                  $font-size-base * 1.5;
$blockquote-footer-font-size:           $font-size-base * .875;
$blockquote-color:                      $heading-color-base;
$blockquote-line-height:                1.333334;
$blockquote-small-color:                #a1a1a1;
$blockquote-footer-margin:              .625rem;

$mark-bg:                               #f4f6f7;

// Tables
//
// Customizes the `.table` component with basic values, each used across all table variations.
$table-cell-padding:                    .875rem;
$table-cell-padding-sm:                 .3125rem .625rem;
$table-active-bg:                       rgba($black, .05);
$table-border-color:                    #e0e0e0;
