$table-light-2-accent-bg:               rgba($white, .1);

// Buttons
//
// For each of Bootstrap's buttons, define text, background, and border color.
$btn-color:                             $white;
$btn-light-color:                       $viridian-100;
$btn-default-bg:                        #bdc3c7;
$btn-font-size:                         $font-size-base * .75;
$btn-font-size-lg:                      $font-size-base * .875;
$btn-font-size-sm:                      $font-size-base * .6875;
$btn-font-weight:                       $font-weight-600;

$btn-padding-y:                         .625rem;
$btn-padding-x:                         1.625rem;
$btn-line-height:                       1.6;

$btn-padding-y-sm:                      .3125rem;
$btn-padding-x-sm:                      1rem;
$btn-line-height-sm:                    1.5;

$btn-padding-y-lg:                      .9375rem;
$btn-padding-x-lg:                      1.9375rem;
$btn-line-height-lg:                    1.45;

$btn-border-width:                      $border-width * 2;

$btn-round-radius:                      1.3125rem;
