$btn-icon-only-size:                    42px;
$btn-icon-only-size-lg:                 55px;
$btn-icon-only-size-sm:                 30px;

// Button shadows
$shadow-size-front:                     0 12px 35px -10px;
$shadow-size-mid:                       0 8px 10px -5px;
$shadow-size-back:                      0 4px 25px -2px;
$shadow-size-back-light:                0 8px 25px -2px;
$shadow-size-focus:                     0 1px 9px 0;
$shadow-size-focus-light:               0 1px 10px 0;

$shadow-opacity-front:                  .55;
$shadow-opacity-mid:                    .09;
$shadow-opacity-back:                   .14;
$shadow-opacity-back-light:             .12;
$shadow-opacity-focus:                  .5;
